import "./App.css";
import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";

// Components

// Pages
import Home from "./Pages/Home";
import Movies from "./Pages/Movies";
import MoviePage from "./Pages/MoviePage";
import NotFound from "./Pages/NotFound";

const defaultPath = "/";

function App() {
   return (
      <div className="App">
         <Router>
            <Switch>
               <Route exact path={`${defaultPath}+""`} component={Home} />
               <Route exact path={`${defaultPath}movies`} component={Movies} />
               <Route
                  exact
                  path={`${defaultPath}movies/:id`}
                  component={MoviePage}
               />
               <Route
                  exact
                  path={`${defaultPath}?search`}
                  component={MoviePage}
               />
               {/* Needs to be at bottom */}
               <Route path="*" component={NotFound} />
            </Switch>
         </Router>
      </div>
   );
}

export default App;
